<template>
    <div class="comment">
        <h3></h3>
        <hr>
        <textarea placeholder="200" maxlength="200" v-model="view"></textarea>
        <mt-button type="primary" size="large" @click.native="getView"></mt-button>
        <div class='comment-list' v-for="(item,index) of allView" :key="index">
            <div class='comment-user'>
                {{index+1}}&nbsp;&nbsp;&nbsp;&nbsp;:{{time|data|addzero}}
            </div>
            <div class='comment-body' v-html="item"></div>

        </div>
        <mt-button size="large" plain></mt-button>
    </div>
</template>
<script>
export default {
    data() {
        return {
            view:'',
            allView:[],
            time:'',
        }
    },
    methods: {
        getView(){
           this.allView.push(this.view);
           this.time=new Date();
            
        }
    },
}
</script>
<style scoped>
    .comment{margin-top: 70px;}
    .comment-list{margin-top: 5px;}
    .comment-user{background:#ccc;font-size: 16px;}
    .comment-body{font:16px "";padding: 5px;}
    
</style>